"use client";

import { useState, useEffect } from "react";
import { useAwsCredentials } from "@/hooks/useAwsCredentials";
import { motion } from "framer-motion";
import CredentialSelector from "@/components/aws/CredentialSelector";
import { Bell, Shield } from "lucide-react";
import Link from "next/link";

interface SecurityCheck {
  id: string;
  title: string;
  description: string;
  status: "passed" | "failed" | "warning" | "info";
  resourceType: string;
  resourceId?: string;
  impact: "critical" | "high" | "medium" | "low";
  recommendation: string;
}

export default function SecurityPage() {
  const { selectedCredential } = useAwsCredentials();
  const [securityChecks, setSecurityChecks] = useState<SecurityCheck[]>([]);
  const [loading, setLoading] = useState(true);
  const [backgroundLoading, setBackgroundLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [scanning, setScanning] = useState(false);
  const [scanProgress, setScanProgress] = useState(0);
  const [filterStatus, setFilterStatus] = useState<string | null>(null);
  const [filterImpact, setFilterImpact] = useState<string | null>(null);
  const [lastCredentialId, setLastCredentialId] = useState<string | null>(null);

  useEffect(() => {
    // Tenta restaurar dados do cache ao iniciar
    if (!selectedCredential) {
      setLoading(false);
      return;
    }

    try {
      // Verificar se já temos dados no localStorage
      const securityCache = localStorage.getItem('securityChecks');
      const cachedCredentialId = localStorage.getItem('securityLastCredentialId');

      if (securityCache && cachedCredentialId === selectedCredential.id) {
        // Usar dados do cache
        const parsedChecks = JSON.parse(securityCache);
        setSecurityChecks(parsedChecks);
        setLoading(false);
        
        // Atualizar em background após 5 minutos do último carregamento
        const lastUpdateTime = localStorage.getItem('securityLastUpdateTime');
        if (lastUpdateTime) {
          const timeSinceLastUpdate = Date.now() - parseInt(lastUpdateTime);
          const fiveMinutesInMs = 5 * 60 * 1000;
          
          if (timeSinceLastUpdate > fiveMinutesInMs) {
            // Atualiza em background se passou mais de 5 minutos
            fetchSecurityIssuesInBackground();
          }
        }
      } else {
        // Se não tem cache ou a credencial mudou, buscar novos dados
        fetchSecurityIssues();
      }
      
      setLastCredentialId(selectedCredential.id);
    } catch (error) {
      console.error("Erro ao restaurar cache:", error);
      fetchSecurityIssues();
    }
  }, [selectedCredential]);
  
  // Função para atualizar dados em background
  const fetchSecurityIssuesInBackground = async () => {
    if (!selectedCredential) return;
    
    try {
      setBackgroundLoading(true);
      
      // Buscar problemas de segurança reais
      const response = await fetch(`/api/aws/resources?credentialId=${selectedCredential.id}`);
      
      if (!response.ok) {
        throw new Error("Erro na atualização em background");
      }
      
      const data = await response.json();
      
      // Processar os problemas de segurança
      const issues = data.securityIssues || [];
      
      // Adicionar verificações simuladas para demonstração (manter consistência)
      const demoChecks: SecurityCheck[] = [
        {
          id: "sec-1",
          title: "Acesso ao Root Account não protegido por MFA",
          description: "A conta root AWS não tem autenticação multifator (MFA) habilitada",
          status: "failed",
          resourceType: "IAM",
          resourceId: "root",
          impact: "critical",
          recommendation: "Ative a autenticação multifator (MFA) para a conta root da AWS"
        },
        {
          id: "sec-2",
          title: "Políticas IAM muito permissivas",
          description: "Usuários com permissões administrativas ('*:*') completas",
          status: "warning",
          resourceType: "IAM",
          resourceId: "policy/AdministratorAccess",
          impact: "high",
          recommendation: "Remova permissões excessivas e aplique o princípio de privilégio mínimo"
        },
        {
          id: "sec-3", 
          title: "Rotação de chaves de acesso",
          description: "Chaves de acesso não foram rotacionadas nos últimos 90 dias",
          status: "warning",
          resourceType: "IAM",
          resourceId: selectedCredential.id,
          impact: "medium",
          recommendation: "Rotacione as chaves de acesso regularmente (a cada 90 dias)"
        }
      ];
      
      // Mapear os problemas de segurança reais para o formato de verificação
      const mappedIssues: SecurityCheck[] = issues.map((issue: any) => ({
        id: `aws-${Math.random().toString(36).substring(7)}`,
        title: issue.title,
        description: issue.description,
        status: issue.severity === "CRITICAL" || issue.severity === "HIGH" ? "failed" : "warning",
        resourceType: issue.resourceType,
        resourceId: issue.resourceId,
        impact: issue.severity.toLowerCase(),
        recommendation: issue.description.includes("público") 
          ? "Restrinja o acesso público e configure ACLs adequadas" 
          : "Revise as configurações de segurança deste recurso"
      }));
      
      // Combinar verificações demo com reais
      const allChecks = [...demoChecks, ...mappedIssues];
      
      // Adicionar verificações passadas para demonstração
      const passedChecks: SecurityCheck[] = [
        {
          id: "sec-4",
          title: "Registro de atividade CloudTrail",
          description: "O CloudTrail está habilitado e configurado corretamente",
          status: "passed",
          resourceType: "CloudTrail",
          impact: "high",
          recommendation: "Continue monitorando os logs do CloudTrail regularmente"
        },
        {
          id: "sec-5",
          title: "Criptografia de volumes EBS",
          description: "Os volumes EBS estão criptografados",
          status: "passed",
          resourceType: "EC2",
          impact: "medium",
          recommendation: "Mantenha a criptografia habilitada para todos os novos volumes"
        }
      ];
      
      const finalChecks = [...allChecks, ...passedChecks];
      setSecurityChecks(finalChecks);
      
      // Salvar no cache
      localStorage.setItem('securityChecks', JSON.stringify(finalChecks));
      localStorage.setItem('securityLastCredentialId', selectedCredential.id);
      localStorage.setItem('securityLastUpdateTime', Date.now().toString());
      
      console.log("Atualização em background de verificações de segurança concluída");
    } catch (err) {
      console.error("Erro na atualização em background:", err);
      // Não exibimos erros de background para o usuário
    } finally {
      setBackgroundLoading(false);
    }
  };

  const fetchSecurityIssues = async () => {
    if (!selectedCredential) {
      setLoading(false);
      return;
    }
    
    setLoading(true);
    setError(null);
    
    try {
      // Simular um scan progressivo para UX melhor
      setScanning(true);
      setScanProgress(0);
      
      const scanTimer = setInterval(() => {
        setScanProgress(prev => {
          if (prev >= 95) {
            clearInterval(scanTimer);
            return 95;
          }
          return prev + Math.floor(Math.random() * 5) + 1;
        });
      }, 300);
      
      // Buscar problemas de segurança reais
      const response = await fetch(`/api/aws/resources?credentialId=${selectedCredential.id}`);
      
      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || "Erro ao buscar problemas de segurança");
      }
      
      const data = await response.json();
      
      // Processar os problemas de segurança
      const issues = data.securityIssues || [];
      
      // Adicionar algumas verificações simuladas para demonstração
      const demoChecks: SecurityCheck[] = [
        {
          id: "sec-1",
          title: "Acesso ao Root Account não protegido por MFA",
          description: "A conta root AWS não tem autenticação multifator (MFA) habilitada",
          status: "failed",
          resourceType: "IAM",
          resourceId: "root",
          impact: "critical",
          recommendation: "Ative a autenticação multifator (MFA) para a conta root da AWS"
        },
        {
          id: "sec-2",
          title: "Políticas IAM muito permissivas",
          description: "Usuários com permissões administrativas ('*:*') completas",
          status: "warning",
          resourceType: "IAM",
          resourceId: "policy/AdministratorAccess",
          impact: "high",
          recommendation: "Remova permissões excessivas e aplique o princípio de privilégio mínimo"
        },
        {
          id: "sec-3", 
          title: "Rotação de chaves de acesso",
          description: "Chaves de acesso não foram rotacionadas nos últimos 90 dias",
          status: "warning",
          resourceType: "IAM",
          resourceId: selectedCredential.id,
          impact: "medium",
          recommendation: "Rotacione as chaves de acesso regularmente (a cada 90 dias)"
        }
      ];
      
      // Mapear os problemas de segurança reais para o formato de verificação
      const mappedIssues: SecurityCheck[] = issues.map((issue: any) => ({
        id: `aws-${Math.random().toString(36).substring(7)}`,
        title: issue.title,
        description: issue.description,
        status: issue.severity === "CRITICAL" || issue.severity === "HIGH" ? "failed" : "warning",
        resourceType: issue.resourceType,
        resourceId: issue.resourceId,
        impact: issue.severity.toLowerCase(),
        recommendation: issue.description.includes("público") 
          ? "Restrinja o acesso público e configure ACLs adequadas" 
          : "Revise as configurações de segurança deste recurso"
      }));
      
      // Combinar verificações demo com reais
      const allChecks = [...demoChecks, ...mappedIssues];
      
      // Adicionar verificações passadas para demonstração
      const passedChecks: SecurityCheck[] = [
        {
          id: "sec-4",
          title: "Registro de atividade CloudTrail",
          description: "O CloudTrail está habilitado e configurado corretamente",
          status: "passed",
          resourceType: "CloudTrail",
          impact: "high",
          recommendation: "Continue monitorando os logs do CloudTrail regularmente"
        },
        {
          id: "sec-5",
          title: "Criptografia de volumes EBS",
          description: "Os volumes EBS estão criptografados",
          status: "passed",
          resourceType: "EC2",
          impact: "medium",
          recommendation: "Mantenha a criptografia habilitada para todos os novos volumes"
        }
      ];
      
      // Atrasar um pouco para simular o processamento
      setTimeout(() => {
        const finalChecks = [...allChecks, ...passedChecks];
        setSecurityChecks(finalChecks);
        setScanning(false);
        setScanProgress(100);
        setLoading(false);
        
        // Salvar no cache
        localStorage.setItem('securityChecks', JSON.stringify(finalChecks));
        localStorage.setItem('securityLastCredentialId', selectedCredential.id);
        localStorage.setItem('securityLastUpdateTime', Date.now().toString());
      }, 2000);
      
      clearInterval(scanTimer);
    } catch (err) {
      console.error("Erro ao buscar problemas de segurança:", err);
      setError(err instanceof Error ? err.message : "Erro ao buscar problemas de segurança");
      setScanning(false);
      setLoading(false);
    }
  };

  const runNewScan = () => {
    fetchSecurityIssues();
  };

  // Filtrar verificações
  const filteredChecks = securityChecks.filter(check => {
    if (filterStatus && check.status !== filterStatus) return false;
    if (filterImpact && check.impact !== filterImpact) return false;
    return true;
  });

  // Contadores para resumo
  const criticalCount = securityChecks.filter(c => c.impact === "critical").length;
  const highCount = securityChecks.filter(c => c.impact === "high").length;
  const mediumCount = securityChecks.filter(c => c.impact === "medium").length;
  const lowCount = securityChecks.filter(c => c.impact === "low").length;
  const passedCount = securityChecks.filter(c => c.status === "passed").length;
  const failedCount = securityChecks.filter(c => c.status === "failed").length;
  const warningCount = securityChecks.filter(c => c.status === "warning").length;

  // Helper para cores
  const getStatusColor = (status: string) => {
    switch (status) {
      case "passed": return "bg-green-100 text-green-800";
      case "failed": return "bg-red-100 text-red-800";
      case "warning": return "bg-yellow-100 text-yellow-800";
      case "info": return "bg-blue-100 text-blue-800";
      default: return "bg-gray-100 text-gray-800";
    }
  };
  
  const getImpactColor = (impact: string) => {
    switch (impact) {
      case "critical": return "bg-red-100 text-red-800";
      case "high": return "bg-orange-100 text-orange-800";
      case "medium": return "bg-yellow-100 text-yellow-800";
      case "low": return "bg-blue-100 text-blue-800";
      default: return "bg-gray-100 text-gray-800";
    }
  };

  return (
    <div>
      <motion.div
        className="mb-6"
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.3 }}
      >
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
          <div>
            <h1 className="text-3xl font-bold text-gray-900 flex items-center">
              <Bell className="mr-2 h-6 w-6 text-indigo-600" />
              Alertas de Segurança
            </h1>
            <p className="mt-2 text-gray-600">
              Monitore e gerencie alertas de segurança dos seus recursos AWS.
            </p>
          </div>
          <div className="flex gap-2 self-end sm:self-auto">
            <Link href="/dashboard/security/unified">
              <button
                className="px-4 py-2 bg-indigo-600 text-white rounded-md hover:bg-indigo-700 transition-colors flex items-center gap-1"
              >
                <Shield className="h-4 w-4" />
                <span>Ver Segurança Unificada</span>
              </button>
            </Link>
            
            <button
              onClick={runNewScan}
              disabled={scanning}
              className="px-4 py-2 bg-indigo-600 text-white rounded-md hover:bg-indigo-700 transition-colors flex items-center gap-1"
            >
              {scanning ? (
                <>
                  <div className="animate-spin rounded-full h-4 w-4 mr-2">
                    <div className="w-full h-full border-2 border-t-2 border-b-2 border-white rounded-full"></div>
                  </div>
                  <span>Escaneando...</span>
                </>
              ) : (
                <span>Iniciar Nova Verificação</span>
              )}
            </button>
            <button
              className="px-4 py-2 bg-indigo-600 text-white rounded-md hover:bg-indigo-700 transition-colors flex items-center gap-1"
            >
              <span>Exportar Relatório</span>
            </button>
          </div>
        </div>
      </motion.div>

      <CredentialSelector />

      {!selectedCredential ? (
        <motion.div
          className="bg-white rounded-lg shadow-md p-6 text-center"
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.3 }}
        >
          <p className="text-gray-500 mb-4">Selecione uma credencial AWS para começar a análise de segurança.</p>
        </motion.div>
      ) : (
        <>
          {/* Painel de controle e resumo */}
          <motion.div
            className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6"
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.3 }}
          >
            {/* Cartão de status */}
            <div className="bg-white rounded-lg shadow-md p-4">
              <div className="flex justify-between items-center mb-3">
                <h3 className="text-lg font-medium text-gray-900">Status</h3>
                {backgroundLoading && (
                  <div className="flex items-center text-xs text-gray-500">
                    <div className="animate-spin h-3 w-3 border-t-2 border-b-2 border-indigo-500 mr-1"></div>
                    Atualizando...
                  </div>
                )}
              </div>
              <div className="space-y-2">
                <div className="flex justify-between">
                  <span className="text-sm text-gray-500">Aprovados</span>
                  <span className="text-sm font-medium text-green-600">{passedCount}</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-sm text-gray-500">Falhas</span>
                  <span className="text-sm font-medium text-red-600">{failedCount}</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-sm text-gray-500">Avisos</span>
                  <span className="text-sm font-medium text-yellow-600">{warningCount}</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-sm text-gray-500">Total</span>
                  <span className="text-sm font-medium">{securityChecks.length}</span>
                </div>
              </div>
            </div>
            
            {/* Cartão de severidade */}
            <div className="bg-white rounded-lg shadow-md p-4">
              <h3 className="text-lg font-medium text-gray-900 mb-3">Severidade</h3>
              <div className="space-y-2">
                <div className="flex justify-between">
                  <span className="text-sm text-gray-500">Crítica</span>
                  <span className="text-sm font-medium text-red-600">{criticalCount}</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-sm text-gray-500">Alta</span>
                  <span className="text-sm font-medium text-orange-600">{highCount}</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-sm text-gray-500">Média</span>
                  <span className="text-sm font-medium text-yellow-600">{mediumCount}</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-sm text-gray-500">Baixa</span>
                  <span className="text-sm font-medium text-blue-600">{lowCount}</span>
                </div>
              </div>
            </div>
            
            {/* Cartão de ações */}
            <div className="bg-white rounded-lg shadow-md p-4">
              <h3 className="text-lg font-medium text-gray-900 mb-3">Ações</h3>
              <div className="space-y-3">
                <button
                  onClick={runNewScan}
                  disabled={scanning}
                  className="w-full flex items-center justify-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 disabled:opacity-50"
                >
                  {scanning ? (
                    <>
                      <svg className="animate-spin -ml-1 mr-2 h-4 w-4 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                        <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                        <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                      </svg>
                      Escaneando... {scanProgress}%
                    </>
                  ) : (
                    "Iniciar Nova Verificação"
                  )}
                </button>
                <button
                  className="w-full px-4 py-2 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                >
                  Exportar Relatório
                </button>
              </div>
            </div>
          </motion.div>
          
          {/* Filtros e lista de verificações */}
          <motion.div
            className="bg-white rounded-lg shadow-md overflow-hidden"
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.3, delay: 0.1 }}
          >
            {/* Filtros */}
            <div className="border-b border-gray-200 bg-gray-50 p-4 flex flex-wrap gap-4">
              <div>
                <label htmlFor="filter-status" className="block text-sm font-medium text-gray-700 mb-1">
                  Status
                </label>
                <select
                  id="filter-status"
                  value={filterStatus || ""}
                  onChange={(e) => setFilterStatus(e.target.value || null)}
                  className="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
                >
                  <option value="">Todos</option>
                  <option value="passed">Aprovado</option>
                  <option value="failed">Falha</option>
                  <option value="warning">Aviso</option>
                </select>
              </div>
              <div>
                <label htmlFor="filter-impact" className="block text-sm font-medium text-gray-700 mb-1">
                  Severidade
                </label>
                <select
                  id="filter-impact"
                  value={filterImpact || ""}
                  onChange={(e) => setFilterImpact(e.target.value || null)}
                  className="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
                >
                  <option value="">Todas</option>
                  <option value="critical">Crítica</option>
                  <option value="high">Alta</option>
                  <option value="medium">Média</option>
                  <option value="low">Baixa</option>
                </select>
              </div>
            </div>
            
            {/* Lista de verificações */}
            <div className="divide-y divide-gray-200">
              {loading ? (
                <div className="p-6 text-center">
                  <div className="animate-spin rounded-full h-8 w-8 border-t-2 border-b-2 border-indigo-500 mx-auto mb-4"></div>
                  <p className="text-gray-500">Carregando verificações de segurança...</p>
                </div>
              ) : error ? (
                <div className="p-6 text-center">
                  <div className="p-4 bg-red-50 rounded-md">
                    <p className="text-red-700">{error}</p>
                  </div>
                </div>
              ) : filteredChecks.length === 0 ? (
                <div className="p-6 text-center">
                  <p className="text-gray-500">Nenhuma verificação encontrada com os filtros atuais.</p>
                </div>
              ) : (
                filteredChecks.map((check) => (
                  <div key={check.id} className="p-4 hover:bg-gray-50">
                    <div className="flex items-start">
                      {/* Indicador de status */}
                      <div className={`w-2 h-2 mt-2 rounded-full ${
                        check.status === 'passed' ? 'bg-green-500' : 
                        check.status === 'failed' ? 'bg-red-500' : 
                        check.status === 'warning' ? 'bg-yellow-500' : 'bg-blue-500'
                      } mr-4`}></div>
                      
                      {/* Conteúdo principal */}
                      <div className="flex-1">
                        <div className="flex justify-between mb-2">
                          <h4 className="text-base font-medium text-gray-900">{check.title}</h4>
                          <div className="flex space-x-2">
                            <span className={`px-2 inline-flex text-xs leading-5 font-semibold rounded-full ${getStatusColor(check.status)}`}>
                              {check.status === 'passed' ? 'Aprovado' : 
                               check.status === 'failed' ? 'Falha' : 
                               check.status === 'warning' ? 'Aviso' : 'Info'}
                            </span>
                            <span className={`px-2 inline-flex text-xs leading-5 font-semibold rounded-full ${getImpactColor(check.impact)}`}>
                              {check.impact === 'critical' ? 'Crítica' : 
                               check.impact === 'high' ? 'Alta' : 
                               check.impact === 'medium' ? 'Média' : 'Baixa'}
                            </span>
                          </div>
                        </div>
                        
                        <p className="text-sm text-gray-500 mb-2">{check.description}</p>
                        
                        <div className="mb-2">
                          <span className="text-xs font-medium text-gray-500">Recurso: </span>
                          <span className="text-xs text-gray-700">{check.resourceType}{check.resourceId ? ` (${check.resourceId})` : ''}</span>
                        </div>
                        
                        {check.status !== 'passed' && (
                          <div className="mt-3 p-3 bg-blue-50 rounded-md">
                            <h5 className="text-xs font-medium text-blue-800 mb-1">Recomendação:</h5>
                            <p className="text-sm text-blue-700">{check.recommendation}</p>
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                ))
              )}
            </div>
          </motion.div>
        </>
      )}
    </div>
  );
} 